// section title

.luminix-section-title{
    padding-bottom: 60px;
    @include max (991px){
        padding-bottom: 40px;
        text-align: center;
    }
    &.center{
        max-width: 650px;
        margin: 0 auto;
        text-align: center;
        &.max-width-700{
            max-width: 700px;
        }
    }
    .text2{
        color: var(--light-color2);
    }
    
}
.mt-50{
    margin-top: 50px;
    @include max (991px){
        margin-top: 40px;
    }
    @include max (767px){
        margin-top: 30px;
    }
}
.section{
    background-position: center;
    background-size: cover;
}