.luminix-video{
  &-thumb{
    text-align: right;
    position: relative;
    @include max (991px){
      padding-bottom: 40px;
    }
    img{
      border-radius: 10px;
      object-fit: cover;
      @include max (991px){
        width: 100%;
      }
    }
  }
}


.luminix-popup-video {
  position: absolute;
  top: 50%;
  left: 63%;
  transform: translate(-50%, -50%);
  @include max (1399px){
    left: 50%;
  }
  @include max (767px){
    top: 46%;
  }
}
.luminix-popup-video2 {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.luminix-video-content{
  a{
    position: relative;
    z-index: 0;
  }
  .video-title{
    padding-bottom: 20px;
    color: var(--white-color);
  }
  .video-text{
    color: var(--white-color);
    margin-bottom: 0;
  }
}





@keyframes animName {
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}

.waves {
    position: absolute;
    width: 210px;
    height: 210px;
    background: #2920d2db;
    opacity: 0;
    border-radius: 100%;
    left: -54px;
    top: -54px;
    z-index: 0;
    transform: translate(-50px, -50px);
    -webkit-animation: waves 3s ease-in-out infinite;
    animation: waves 3s ease-in-out infinite;
    z-index: -1;
    @include max(991px) {
      width: 160px;
      height: 160px;
      left: -30px;
      top: -30px;
    }
    &.wave-1 {
      -webkit-animation-delay: 0s;
      animation-delay: 0s;
    }
    &.wave-2 {
      -webkit-animation-delay: 1s;
      animation-delay: 1s;
    }
    &.wave-3 {
      -webkit-animation-delay: 2s;
      animation-delay: 2s;
    }
    &.waves2{
      top: -55px;
      @include max (991px){
        top: -30px;
      }
    }
  }
.play-btn{
  display: flex;
  gap: 15px;
  align-items: center;
  p{
    font-size: 16px;
    font-weight: 700;
    line-height: 24px;
    z-index: 1;
    color: var(--white-color);
  }
}
  
  
  @keyframes waves {
    0% {
        -webkit-transform: scale(0.2, 0.2);
        transform: scale(0.2, 0.2);
        opacity: 0;
    }
  
    50% {
        opacity: 0.9;
    }
  
    100% {
        -webkit-transform: scale(0.9, 0.9);
        transform: scale(0.9, 0.9);
        opacity: 0;
    }
}

.luminix_screenfix_left{
  margin-left: calc((-100vw + 99%) / 2);
  @include max (991px){
      margin-left: auto;
  }
}
.extra-padding{
  margin-left: 50px;
  padding: 70px 0;
  @include max (991px){
    margin-left: 0;
    padding: 60px 0;
  }
}
.luminix-content-thumb{
  height: 100%;
  position: relative;
  img{
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  &::before{
    content: "";
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    position: absolute;
    border-radius: 0 10px 10px 0;
    background: #001A3D1A;
  }
  @include max (991px){
    margin: 80px 0 0;
    height: auto;
    img{
      border-radius: 10px;
    }
  }
  @include max (767px){
    margin: 60px 0 0;
  }
}

.luminix-video-content{
  padding-right: 10px;
  @include max (991px){
    padding-right: 0;
  }
}