.luminix-p{
    &-wrap{
        position: relative;
        &:hover{
            .luminix-p-content{
                opacity: 1;
                bottom: 30px;
            }
            .luminix-p-thumb img{
                scale: 1.1;
            }

        }
    }
    &-thumb{
        position: relative;
        display: block;
        margin-bottom: 24px;
        text-align: center;
        overflow: hidden;
        border-radius: 10px;
        &::before{
            position: absolute;
            content: "";
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            z-index: 1;
            opacity: 0;
            transition: .4s;
            background: linear-gradient(180deg, rgba(41, 32, 210, 0) 0%, rgba(41, 32, 210, 0.6) 100%);
        }
        &:hover::before {
            opacity: 1;
        }
        img{
            border-radius: 10px;
            width: 100%;
            transition: all 0.4s;
            object-fit: cover;
        }
    }
    &-content{
        display: flex;
        z-index: 1;
        align-items: center;
        gap: 24px;
        background-color: #fff;
        border-radius: 10px;
        width: 297px;
        position: absolute;
        bottom: 0;
        left: 30px;
        transition: all .4s;
        opacity: 0;
        height: 90px;
        justify-content: center;
    }
    &-btn{
        width: 50px;
        height: 50px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 5px;
        background-color: var(--accent-bg);
        img{
            transition: all 0.4s;
            transform: rotate(-45deg);
        }
        &:hover{
            img{
                transform: rotate(0);
            }
        }
        
    }
}