.luminix-footer{
  &-menu{
    @include max(767px) {
      margin-bottom: 30px;
    }
    ul{
      li{
        margin-bottom: 8px;
        &:last-child{
          margin-bottom: 0;
        }
        a{
          position: relative;
          transition: all 0.4s;
          color: var(--white-color);
          &:hover{
            color: var(--accent-color);
          }
        }
      }
    }

  }
  &-menu2{
    ul{
      li{
        padding-bottom: 10px;
        color: var(--white-color);
        &:last-child{
          padding-bottom: 0;
        }
        img{
          padding-bottom: 5px;
          padding-right: 5px;
        }
        a{
          color: var(--white-color);
          img{
            padding-right: 5px;
          }
        }
      }
    }
    p{
      color: var(--white-color);
      margin-bottom: 24px;
    }
    @include max(767px) {
      margin-bottom: 30px;
    }
  }
  &-section1{
    padding: 120px 0 0;
    border-bottom: 1px solid #FFFFFF1A;
    background-color: #001A3D;
    @include max (991px){
      padding: 80px 0 0;
    }
    @include max (767px){
      padding: 60px 0 0;
    }
  }
  &-textarea{
    img{
      padding-bottom: 24px;
    }
    p{
      margin-bottom: 24px;
      color: var(--white-color);
    }
    @include max (1399px){
      max-width: 600px;
      padding-bottom: 70px;
    }
    @include max (991px){
      padding-bottom: 30px;
    }
  }
  &-title{
    position: relative;
    padding-bottom: 35px;
    @include max (767px){
      padding-bottom: 25px;
    }
    &::before{
      position: absolute;
      content: "";
      bottom: 25px;
      left: 0;
      width: 55px;
      height: 4px;
      background-color: var(--accent-bg);
      @include max (767px){
        bottom: 15px;
       }
    }
    h5{
      color: var(--white-color);
    }
  }
  &-title2{
    padding-bottom: 35px;
    @include max (767px){
      padding-bottom: 25px;
    }
    h5{
      color: var(--white-color);
    }
  }
  &-bottom-text {
    font-size: 16px;
    line-height: 24px;
    margin-top: 100px;
    padding: 30px 0;
    text-align: center;
    color: var(--white-color);
    border-top: 1px solid #FFFFFF1A;
    @include max (991px){
      margin-top: 80px;
    }
    @include max (767px){
      margin-top: 60px;
    }
    p{
      margin-bottom: 0;
    }
    &.text2{
      display: flex;
      align-items: center;
      justify-content: space-between;
      @include max (767px){
        display: block;
        margin-top: 15px;
      }
    }
  }
  &-bottom-menu{
    @include max (767px){
      margin-top: 15px;
    }
    ul{
      li{
        display: inline-block;
        margin-left: 50px;
        @include max (991px){
          margin-left: 15px;
        }
        a{
          font-size: 16px;
          line-height: 24px;
          color: var(--white-color);
          &:first-child{
            margin-left: 0;
          }
          &.dot{
            position: relative;
            &::before{
              position: absolute;
              content: "";
              top: 9px;
              right: -30px;
              width: 5px;
              height: 5px;
              border-radius: 100px;
              background-color: var(--white-color);
              @include max (991px){
                display: none;
              }
            }
          }
        }
      }
    }
  }
}
.mb-0{
  @include max (767px){
    margin-bottom: 0;
  }
}
.ml-24{
  margin-left: 24px;
  @include max (1399px){
    margin-left: 0;
  }
}
.ml-50{
  @include max (991px){
    margin-left: 50px!important;
  }
  @include max (767px){
    margin-left: 0px!important;
  }
}
.pl-51 {
  padding-left: 50px;
  @include max (1399px){
    padding-left: 0px;
  }
}
.pl-50{
  @include max (991px){
    margin-left: 50px;
  }
  @include max (767px){
    margin-left: 0px;
  }
}
.pl-26{
  padding-left: 26px;
  @include max (1399px){
    padding-left: 0;
  }
}
.luminix-instagram{
  &-wrap {
    gap: 10px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    padding-top: 8px;
    @include max (767px){
      padding-top: 15px;
    }
  }
  &-thumb{
    position: relative;
    overflow: hidden;
    border-radius: 5px;
    transition: all 0.4s;
    &:hover img{
      transform: scale(1.1);
    }
    img{
      width: 100%;
      transition: all .4s;
      object-fit: cover;
    }
  }
}