input[type="text"],
input[type="email"],
input[type="password"],
input[type="url"],
input[type="tel"],
input[type="time"],
input[type="date"],
input[type="search"],
input, textarea {
  margin: 0;
  width: 100%;
  height: 60px;
  font-weight: 400;
  color:var(--heading-color);
  border-radius: 0;
  background-color: transparent;
  padding: 15px 0 ;
  border: none;
  transition: all 0.4s;
}
textarea {
  height: 145px;
  resize: none;
  @include max(767px) {
    height: 100px;
  }
}

input::-webkit-input-placeholder, 
textarea::-webkit-input-placeholder{ /* WebKit, Blink, Edge */
    color:    var(--heading-color);
    opacity: 1;
}
input:-moz-placeholder, 
textarea:-moz-placeholder{ /* Mozilla Firefox 4 to 18 */
   color:    var(--heading-color);
   opacity:  1;
}
input::-moz-placeholder, 
textarea::-moz-placeholder{ /* Mozilla Firefox 19+ */
   color:    var(--heading-color);
   opacity:  1;
}
input:-ms-input-placeholder, 
textarea:-ms-input-placeholder{ /* Internet Explorer 10-11 */
   color:    var(--heading-color);
   opacity: 1;
}
input::-ms-input-placeholder, 
textarea::-ms-input-placeholder{ /* Microsoft Edge */
   color:    var(--heading-color);
   opacity: 1;
}

input::placeholder, 
textarea::placeholder{ /* Most modern browsers support this now. */
   color:    var(--heading-color);
   opacity: 1;
}

// search header v1
.luminix-header-search-box {
   position: relative;
   input{
      font-size: 34px;
      border: none!important;
      border-radius: 0;
      height: 80px;
      border-bottom: 1px solid #E3E3EC!important;
      margin-bottom: 12px;
      &::placeholder{
         color: #8A8C9A;
      }
      &:focus{
         border-bottom: 1px solid var(--accent-color)!important;
      }     
   }
   p{
      color: var(--heading-color);
   }
}
button#header-search {
   position: absolute;
   top: 25px;
   right: 0;
   i{
      font-size: 30px;
      color: var(--accent-color);
   }
}

[type="search"]::-webkit-search-cancel-button,
[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
  appearance: none;
}

// footer sebscription
.luminix-subscription-field{
   input{
      font-size: 16px;
      line-height: 1.33em;
      padding: 13px 24px;
      border-radius: 100px;
      margin-bottom: 12px;
      height: 50px;
      background-color: var(--white-bg);
   }
}
.luminix-cta-input-field{
   display: flex;
   gap: 15px;
   align-items: center;
   padding-top: 10px;
   
   @include max (1399px){
      padding-top: 48px;
   }
   @include max (1199px){
      justify-content: center;
      padding-top: 30px;
      flex-wrap: wrap;
   }
   input{
       padding: 18px 24px;
       border-radius: 3px;
       color: var(--white-color);
       background-color: #FFFFFF4D;
       width: 64%;
       @include max (767px){
         width: 100%;
       }
   }
}

.luminix-cta-input-field input::-webkit-input-placeholder, 
textarea::-webkit-input-placeholder{ /* WebKit, Blink, Edge */
    color:    var(--white-color);
    opacity: 1;
}
.luminix-cta-input-field input:-moz-placeholder, 
textarea:-moz-placeholder{ /* Mozilla Firefox 4 to 18 */
   color:    var(--white-color);
   opacity:  1;
}
.luminix-cta-input-field input::-moz-placeholder, 
textarea::-moz-placeholder{ /* Mozilla Firefox 19+ */
   color:    var(--white-color);
   opacity:  1;
}
.luminix-cta-input-field input:-ms-input-placeholder, 
textarea:-ms-input-placeholder{ /* Internet Explorer 10-11 */
   color:    var(--white-color);
   opacity: 1;
}
.luminix-cta-input-field input::-ms-input-placeholder, 
textarea::-ms-input-placeholder{ /* Microsoft Edge */
   color:    var(--white-color);
   opacity: 1;
}

.luminix-cta-input-field input::placeholder, 
textarea::placeholder{ /* Most modern browsers support this now. */
   color:    var(--white-color);
   opacity: 1;
}
// contact box start

.luminix-contact{
   &-thumb{
      @include max (991px){
         padding-bottom: 30px;
      }
       img{
           border-radius: 10px;
           @include max (991px){
            width: 100%;
         }
       }
   }
   &-box{
       background: var(--light-color);
       border-radius: 10px;
       padding: 30px;
   }
   &-title{
      padding-bottom: 30px;
      &.title2{
         padding-bottom: 50px;
         @include max (991px){
            padding-bottom: 40px;
         }
         @include max (767px){
            padding-bottom: 30px;
         }
      }
   }
}

.luminix-main-field{
   input{
      background-color: var(--white-bg);
      padding: 18px 30px;
      border-radius: 50px;
      border: 1px solid #001A3D1A;
      margin-bottom: 30px;
      font-size: 16px;
      font-weight: 400;
      line-height: 24px;
      color: #001A3D80;
   }
   &-textarea{
      background-color: var(--white-bg);
      border: 1px solid #001A3D1A;
      padding: 18px 20px 20px 30px;
      border-radius: 20px;
      margin-bottom: 24px;
      height: 180px;
      textarea{
         padding: 0;
      }
   }
   &.field2{
      input{
         border: none;
         &::placeholder{
            color: var(--heading-color);
         }
      }
   }
}
.luminix-main-field input::-webkit-input-placeholder, 
textarea::-webkit-input-placeholder{ /* WebKit, Blink, Edge */
    color: #001A3D80;
    opacity: 1;
}
.luminix-main-field input:-moz-placeholder, 
