.luminix-about{
    &-thumb{
        position: relative;
        @include max (991px){
            text-align: center;
            padding-bottom: 40px;
        }
        img{
            border-radius: 10px;
            @include max (991px){
                width: 100%;
            }
        }
    }
    &-card{
        padding: 20px;
        border-radius: 10px;
        background-color: var(--accent-color);
        position: absolute;
        bottom: -40px;
        right: 100px;
        h2,h5{
            color: var(--white-color);
        }
        @include max (1399px){
            right: 15px;
        }
        @include max (1199px){
            right: 0;
        }
        @include max (991px){
            right: 0;
            bottom: 40px;
        }
        @include max (767px){
            display: none;
        }
    }
    // end one
    &-thumb2{
        position: relative;
        text-align: right;
        @include max (1399px){
            display: none;
           
        }

        img{
            border-radius: 10px;
            @include max (1399px){
                width: 100%;
            }
        }
    }
    &-thumb4{
        display: none;
        @include max (1399px){
            display: block;
            text-align: right;
            img{
                border-radius: 10px;
                width: 80%;
                height: 600px;
                object-fit: cover;
            }
        }
        @include max (991px){
            padding-bottom: 40px;
            margin-bottom: 0;
            text-align: center;
            img{
                width: 60%;
                height: 600px;
                object-fit: cover;
            }
        }
        @include max (767px){
            img{
                height: 500px;
            }
        }
        @include max (575px){
            img{
                width: 100%;
            }
        }
    }
    &-thumb3{
        position: absolute;
        bottom: -80px;
        left: 56px;
        @include max (1399px){
            display: none;
        }
    }
    &-card2{
        display: table;
        background: var(--accent-color);
        position: absolute;
        top: 0;
        left: 56px;
        padding: 14px 20px;
        border-radius: 10px;
        width: 280px;
        @include max (1399px){
            display: none;
        }
        p{
            text-align: left;
            font-size: 18px;
            line-height: 30px;
            font-weight: 600;
            color: var(--white-color);
            padding-top: 14px;
        }
    }
    &-card-icon{
       display: flex;
       gap: 10px;
       align-items: center;
    }
    // end two
    &-thumb5{
        position: relative;
        @include max (991px){
            text-align: center;
            img{
                width: 100%;
            }
        }
    }
    &-card3{
        width: 478px;
        padding: 20px;
        border-radius: 5px;
        position: absolute;
        bottom: 24px;
        left: 24px;
        background-color: var(--accent-color);
        img{
            padding-bottom: 5px;
            display: inline-block;
        }
        h6{
            color: var(--white-color);
        }
        @include max (1399px){
            width: 400px;
        }
        @include max (1199px){
            width: 330px;
        }
        @include max (991px){
            display: none;
        }
    }
    // inner page
    &-section{
        padding-top: 120px;
        @include max (1199px){
            padding-top: 100px;
        }
        @include max (991px){
            padding-top: 0;
        }
    }
    &-author-wrap{
        display: flex;
        align-items: center;
        gap: 15px;
        margin-top: 24px;
    }
    &-author-thumb{
        img{
            border-radius: 100px;
            margin-top: 3px;
        }
    }
    &-author-data{
        h6{
            color: var(--heading-color);
            margin-bottom: 5px;
        }
        p{
            font-size: 14px;
            font-weight: 400;
            line-height: 20px;
            color: var(--heading-color);
        }
    }
}
.mt-120{
    margin-top: 120px;
    @include max (1199px){
        margin-top: 100px;
    }
    @include max (991px){
        margin-top: 80px;
    }
    @include max (767px){
        margin-top: 60px;
    }
}

.title{
    padding: 15px 0 20px 0;
}
.text{
    margin-bottom: 24px;
    color: var(--heading-color);
}
.text3{
    margin-bottom: 0;
    padding-bottom: 24px;
}
.pl-100{
    padding-left: 100px;
    @include max (991px){
        padding-left: 0;
        padding-top: 0;
    }
}
.title2{
    padding-bottom: 20px;
}
.text4{
    margin: 0;
    padding-bottom: 24px;
    color: var(--heading-color);
}
.text5{
    margin: 0;
    padding-bottom: 30px;
    color: var(--heading-color);
    border-bottom: 1px solid #001A3D1A;
    margin-bottom: 30px;
    padding-right: 50px;
    @include max (991px){
        padding-right: 0;
    }
}