.luminix-service{
    &-card {
        position: absolute;
        bottom: -50px;
        right: 100px;
        @include max (1399px){
            right: 30px;
        }
        @include max (1199px){
            right: 0;
        }
        @include max (991px){
            display: none;
        }
    }
    &-wrap{
        text-align: center;
        border-radius: 5px;
        padding: 20px;
        margin-bottom: 24px;
        transition: all 0.4s;
        border: 2px solid var(--light-color);
        &:hover{
            box-shadow: 0px 4px 80px 0px #00000014;
        }
    }
    &-icon{
        padding-bottom: 20px;
    }
    &-content{
        padding-bottom: 24px;
        h5{
            padding-bottom: 15px;
        }
        p{
            font-size: 16px;
            line-height: 24px;
            color: var(--light-color2);
        }
    }
    &-btn{
        a{
            font-size: 16px;
            line-height: 24px;
            font-weight: 700;
            color: var(--heading-color);
            svg{
                transition: all 0.4s;
                margin-left: 5px;
                margin-top: -5px;
            }
            &:hover{
                color: var(--accent-color);
                svg path{
                    stroke: var(--accent-color);
                }
            }
        }
    }
    // inner page
    &-d-wrap{
        padding-right: 120px;
        @include max (991px){
            padding-right: 0;
        }
    }
    &-d-thumb{
        margin-bottom: 24px;
        position: relative;
        overflow: hidden;
        border-radius: 10px;
        &:hover{
            img{
                transform: scale(1.1);
            }
        }
        img{
            width: 100%;
            height: 100%;
            transition: all 0.4s;
            object-fit: cover;
        }
    }
    &-d-data{
        p{
            color: var(--heading-color);
        }
    }
    &-d-content{
        padding-top: 30px;
        h3{
            padding-bottom: 20px;
        }
        p{
            margin-bottom: 20px;
            color: var(--heading-color);
            span{
                font-family: $heading-font;
                font-size: 20px;
                font-weight: 700;
                line-height: 30px;
            }
        }
    }
    &-d-menu{
        ul{
            li{
                padding-bottom: 15px;
                border-bottom: 1px solid rgba(0,26,61,.1019607843);
                margin-bottom: 15px;
                &:last-child{
                    padding-bottom: 0;
                    border-bottom: none;
                    margin-bottom: 0;
                }
                a{
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    color: var(--heading-color);
                    transition: all 0.4s;
                    &:hover{
                        color: var(--accent-color);
                        path{
                            stroke: var(--accent-color);
                        }
                    }
                }
            }
        }
    }
}

.ml-20{
    margin-right: 20px;
    @include max (991px){
        margin-right: 0;
    }
}
.service-btn{
    margin-top: 26px;
    text-align: center;
}
.luminix-content{
    &-thumb2{
        @include max (991px){
            padding-bottom: 40px;
        }
        @include max (767px){
            padding-bottom: 30px;
        }
        img{
            border-radius: 10px;
            @include max (991px){
                width: 100%;
            }
        }
    }
}