
.luminix-default-btn,
.luminix-subcribe-btn,
#luminix-submit-btn {
  font-size: $button_font_size;
  line-height: $buton_line_height;
  padding: $button_padding;
  font-weight: $button_font_weight;
  font-family: $button_font_family;
  width: fit-content;
  position: relative;
  z-index: 1;
  border-radius: 6px;
  white-space: nowrap;
  text-overflow: ellipsis;
  display: inline-flex;
  text-align: center;
  -o-transition: all .4s ease-in-out;
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
  overflow: hidden;
  color: var(--white-color) !important;
  background-color: var(--accent-bg);
  position: relative;
  z-index: 0;
  overflow: hidden;
  position: relative;
  transform-style: preserve-3d;
  img{
    padding-left: 10px;
    padding-top: 3px;
  }
  svg{
    margin-left: 10px;
    margin-top: 3px;
    transition: all 0.4s;
  }
  &.subcription-btn2{
    width: 100%;
    height: 50px;
    padding: 11px 44px;
    display: flex;
    justify-content: center;
    background-color: var(--accent-bg);
    &:hover{
      color: var(--heading-color)!important;
      background-color: var(--white-bg);
    }
  }
  &.faq-btn{
    color: var(--heading-color)!important;
    background-color: var(--white-bg);
    border: 1px solid #001A3D1A;
    margin-bottom: 15px;
    &:last-child{
      margin-bottom: 0;
    }
    &:hover{
      color: var(--white-color)!important;
      background-color: var(--accent-bg);
      border: 1px solid transparent;
      svg path{
        stroke: var(--white-color)!important;
        fill: var(--white-color)!important;
      }
    }
  }
  &.luminix-header-btn{
    font-size: 16px;
    padding: 15px 38px;
    border-radius: 100px;
    color: var(--heading-color)!important;
    background-color: var(--white-bg);
    border: 2px solid var(--body-color);
    img{
      padding-top: 0;
      margin-left: 10px;
    }
    @include max (767px){
      display: none;
    }
  }
  &.luminix-header-btn2{
    font-size: 16px;
    padding: 17px 40px;
    border-radius: 3px;
    color: var(--white-color)!important;
    background-color: var(--accent-bg);
    @include max (767px){
      display: none;
    }
  }
  &.luminix-header-btn3{
    font-size: 16px;
    padding: 17px 40px;
    border-radius: 3px;
    color: var(--heading-color)!important;
    background-color: var(--white-bg);
    @include max (767px){
      display: none;
    }
  }
  // start-hero-btn
  &.luminix-hero-btn2{
    border-radius: 3px!important;
    color: var(--white-color)!important;
      background-color: var(--accent-bg);
    &:hover{
      color: var(--heading-color)!important;
      background-color: var(--white-bg);
    }
  }
  &.luminix-hero-btn3{
    border-radius: 3px!important;
    color: var(--heading-color)!important;
      background-color: var(--white-bg);
    &:hover{
      color: var(--white-color)!important;
      background-color: var(--accent-bg);
    }
  }
  // start cta btn
  &.cta-btn3{
    border-radius: 6px;
    background: none;
    border: 2px solid #fff;
    &:hover{
      border: 2px solid transparent;
     background-color: var(--accent-bg);
    }
  }
  &.cta-btn2{
    background-color: var(--accent-bg);
    border: 2px solid var(--accent-bg);
    border-radius: 6px;
    &:hover{
      border: 2px solid var(--white-color);
      background-color: transparent!important;
    }
  }
  &.cta-btn{
    padding: 17px 22px;
    color: var(--heading-color)!important;
    background-color: var(--white-bg);
    &:hover{
      color: var(--white-color)!important;
      background-color: var(--heading-color);
    }
  }
  &.disabled,
  &:disabled {
    opacity: 1;
  }

  &.d-block {
    width: 100%;
  }

  &.pill {
    border-radius: 50px;
  }

  &.btn-icon {
    padding: 9px 24px;
  }

  &:focus {
    box-shadow: none;
  }
  &.pricing-btn-v4:hover {
    color: var(--heading-color)!important;
    background-color: var(--accent-color);
  }
  &.pricing-btnv4:hover {
    color: var(--white-color)!important;
    background-color: var(--heading-color);
  }
  
}


.luminix-default-btn::before,
#luminix-submit-btn::before {
  position: absolute;
  content: '';
  border-radius: inherit;
  -webkit-transition: all 0.4s ease;
  transition: all 0.4s ease;
  z-index: -1;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-size: 102% 102%;
  opacity: 0;
}
.luminix-default-btn .button-wraper, 
#luminix-submit-btn .button-wraper {
  transition: opacity 0.2s, transform 0.2s;
  transition-timing-function: cubic-bezier(0.455, 0.03, 0.515, 0.955);
}
.luminix-default-btn:hover .button-wraper, 
#luminix-submit-btn:hover .button-wraper {
  transform: translateY(-150%);
  opacity: 0;
}
.luminix-default-btn:hover::before, 
#luminix-submit-btn:hover::before  {
  opacity: 1;
  transition: all 0.2s cubic-bezier(0.455, 0.03, 0.515, 0.955);
}
.luminix-default-btn:hover::after, 
#luminix-submit-btn:hover::after {
  transform: translate(0%, -50%);
  opacity: 1;
}


.luminix-default-btn:after, 
#luminix-submit-btn:after {
  content: attr(data-text);
  display: inline-block;
  position: absolute;
  top: 50%;
  opacity: 0;
  line-height: 12.5px;
  transform: translate(0, 100%);
  transition: opacity 0.2s, transform 0.2s;
  transition-timing-function: cubic-bezier(0.455, 0.03, 0.515, 0.955);
  white-space: nowrap;
}




.luminix-title-btn {
  @include max (991px){
    width: 100%;
    text-align: center;
    margin-top: 30px;
  }
}