.luminix-t{
    &-wrap{
        padding: 20px;
        border-radius: 10px;
        background-color: var(--white-bg);
        &.wrap2{
            margin-bottom: 24px;
        }
    }
    &-header{
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &-content{
        padding: 30px 0 24px;
        p{
            font-size: 21px;
            margin-bottom: 0;
            color: var(--heading-color);
        }
    }
    &-author{
        display: flex;
        align-items: center;
        gap: 24px;
        &-data{
            h6{
                color: var(--heading-color);
            }
            p{
                color: var(--heading-color);
            }
        }
    }
    // end one
    &-wrap2{
        text-align: center;
        border-radius: 10px;
        margin-bottom: 24px;
    }
    &-ratting{
        padding-bottom: 30px;
    }
    &-content2{
        padding-bottom: 24px;
        p{
            font-size: 21px;
            line-height: 30px;
            color: var(--heading-color);
        }
    }
    &-author2{
        display: flex;
        align-items: center;
        gap: 24px;
        justify-content: center;
        &-data2{
            h6{
                text-align: left;
                color: var(--heading-color);
            }
            p{
                font-size: 16px;
                line-height: 24px;
                color: var(--heading-color);
                text-align: left;
            }
        }
    }
    
}
.luminix-testimonial-slider .slick-slide {
    margin: 0 12px;
}
.luminix-testimonial-slider .slick-dots button {
    display: block;
    padding: 0;
    border: none;
    text-indent: -9999px;
    transition: .3s;
    width: 10px;
    height: 10px;
    background: #001A3D1A;
    transition: all 0.4s;
    border-radius: 100px;
}
.luminix-testimonial-slider ul.slick-dots {
    bottom: -80px;
    left: 50%;
    position: absolute;
    display: flex;
    gap: 15px;
    transform: translate(-50%, -50%);
    @include max (1199px){
        bottom: -70px;
    }
    @include max (767px){
        display: none!important;
    }
}
.luminix-testimonial-slider .slick-dots li {
    display: flex;
    align-items: center;
    justify-content: center;
}
.luminix-testimonial-slider .slick-dots li.slick-active {
    width: 23px;
    height: 23px;
    border-radius: 100px;
    border: 1px solid var(--accent-color);
    button{
        background-color: var(--accent-color);
    }
}