.luminix-hero{
    &-section{
        padding: 130px 0;
        margin-top: 170px;
        position: relative;
        z-index: 0;
        @include max (1199px){
            margin-top: 100px;
        }
        @include max (991px){
            padding: 80px 0;
        }
        @include max (767px){
            margin-top: 75px;
            padding: 60px 0;
        }
        &::before{
            content: "";
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            position: absolute;
            background: linear-gradient(90deg, #001A3D 0%, rgba(0, 26, 61, 0) 100%);
        }
    }
    &-content{
        position: relative;
        max-width: 880px;
        h5{
            color: var(--white-color);
            padding-bottom: 20px;
        }
        .hero-title{
            padding-bottom: 24px;
        }
        .text{
            max-width: 830px;
            font-size: 21px;
            margin-bottom: 0;
            color: var(--white-color);
        }
    }
    // end one
    &-slider-item{
        padding: 250px 0 150px;
        position: relative;
        z-index: 0;
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
        transition: all 0.4s;
        @include max (991px){
            padding: 200px 0 80px;
        }
        @include max (767px){
            margin-top: 75px;
            padding: 100px 0 80px;
        }
        &::before{
            content: "";
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            position: absolute;
            background: #2920D299;
        }
        &:hover{
            .luminix-hero-slider-init .luminix-hero-next, 
            .luminix-hero-slider-init .luminix-hero-prev {
                left: 8%;
            }
        }
    }
    
    &-content2{
        position: relative;
        text-align: center;
        h5{
            color: var(--white-color);
            padding-bottom: 20px;
        }
        .hero-title{
            padding-bottom: 24px;
        }
        .text{
            max-width: 1000px;
            text-align: center;
            font-size: 21px;
            margin: 0 auto 0;
            color: var(--white-color);
        }
    }
    // end two
    &-section3{
        padding-top: 90px;
        @include max (1199px){
            padding-top: 120px;
        }
    }
    &-thumb{
        @include max (1399px){
            padding-top: 30px;
        }
        @include max (991px){
            padding-top: 0;
            text-align: center;
            padding-bottom: 40px;
        }
    }
    &-content3{
        padding-bottom: 30px;
        .hero-title{
            padding-bottom: 24px;
        }
        .text{
            color: var(--white-color);
        }
        @include max (1199px){
            padding-bottom: 50px;
        }
    }
}
.hero-btn{
    display: flex;
    gap: 24px;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    &.btn2{
        justify-content: start;
    }
}
.luminix-hero-slider-init .luminix-hero-next, 
.luminix-hero-slider-init .luminix-hero-prev {
  top: 56%;
  left: 8%;
  width: 60px;
  height: 60px;
  z-index: 1;
  border: none;
  display: flex;
  cursor: pointer;
  position: absolute;
  transition: all .4s;
  align-items: center;
  border-radius: 100px;
  background-size: 20px;
  justify-content: center;
  transform: translateY(-50%);
  background-position: center;
  background-repeat: no-repeat;
  border-radius: 100px;
  border: 1px solid var(--white-color);
  background-image: url(../images/hero/left-arrow.svg);
  &:hover{
    border: 1px solid var(--accent-color);
  }
  @include max (1599px){
    left: 12%;
  }
  @include max (1199px){
    top: 53%;
  }
  @include max (991px){
    left: 4%;
  }
  @include max (767px){
    display: none!important;
  }
}
.luminix-hero-slider-init .luminix-hero-prev {
  left: auto;
  right: 8%;
  background-image: url(../images/hero/right-arrow-white2.svg);
  @include max (1599px){
    right: 12%;
  }
  @include max (991px){
    right: 4%;
  }

}