// fonts variable
$body-font: "Inter", sans-serif!default;
$heading-font: "Plus Jakarta Sans", sans-serif !default;
$blockquote-font: "Afacad", sans-serif !default;


// color variable
:root {
  --heading-color: #001A3D;
  --body-color: #4D5B68;
  --accent-color: #2920D2;
  --accent-color2: #2BC8EB;
  --white-color: #ffffff;
  --white-bg: #ffffff;
  --accent-bg: #2920D2;
  --accent-bg2: #2BC8EB;
  --accent-bg3: #0256FE;
  --light-color: #F7F7F7;
  --light-color2: #2D374A;
  --light-color3: #0D0E1D;
  --light-color4: #13111A;
  --light-color5: #838588;
  --dark-color1: #13111A;
  --dark-color2: #0C0C0C;
}

$colors: (
 
);

$bodyfontScale: (
  xl: (
    20: 20px,
    lineheight: 32px,
  ),
  lg: (
    18: 18px,
    lineheight: 28px,
  ),
  md: (
    16: 16px,
    lineheight: 24px,
  ),
  xs: (
    14: 14px,
    lineheight: 22px,
  ),
  xss: (
    12: 12px,
    lineheight: 20px,
  ),
);

// button font size
$button_font_size: 16px;
$buton_line_height: 26px;
$buton_border_radius: 6px;
$button_padding: 17px 44px;
$button_font_weight: 700;
$button_font_family: $body-font;

