.luminix-blog{
    &-wrap{
        position: relative;
        transition: all 0.4s;
        margin-bottom: 24px;
        &:hover{
            .luminix-blog{
                &-thumb img{
                    transform: scale(1.1);
                }
            }

        }
    }
    &-thumb{
        position: relative;
        overflow: hidden;
        border-radius: 10px 10px 0 0;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: all 0.4s;
            border-radius: 10px 10px 0 0;
        }
    }
    &-frame{
        a{
            font-size: 16px;
            line-height: 20px;
            background: var(--white-bg);
            padding: 10px 20px;
            border-radius: 50px;
            position: absolute;
            top: 20px;
            right: 20px;
            color: var(--heading-color);
            font-weight: 600;
        }
    }
    &-content{
        padding: 30px;
        border-radius: 0 0 10px 10px;
        background-color: var(--white-bg);
        .blog-title{
            padding-bottom: 24px;
        }
    }
    &-meta{
        padding-bottom: 15px;
        transition: all .4s;
        line-height: 1;
        display: inline-block;
        a{
            color: var(--accent-color);
            margin-right: 15px;
            font-size: 16px;
            line-height: 24px;
            img{
                margin-right: 10px;
                margin-top: -4px;
            }
            &:last-child{
                margin-right: 0;
            }
        }
        &.meta2{
            padding: 30px 0 24px;
        }
    }
    &-btn{
        a{
            font-size: 16px;
            line-height: 24px;
            font-weight: 700;
            color: var(--heading-color);
            svg{
                margin-left: 5px;
                transition: all 0.4s;
                margin-top: -5px;
            }
            &:hover{
                color: var(--accent-color);
                path{
                    stroke: var(--accent-bg);
                    fill: var(--accent-bg);
                }
            }
        }
        &.btn2{
            a {
                transition: all 0.4s;
                color: var(--heading-color);
                svg{
                    margin-top: -4px;
                    margin-left: 5px;
                }
                &:hover{
                    color: var(--accent-color);
                    path{
                        stroke: var(--accent-color);
                        fill: var(--accent-color);
                    }
                }
            }
        }
    }
    // end one
    &-wrap2{
        border-radius: 10px;
        background-color: var(--white-bg);
        @include max (1199px){
            margin-bottom: 24px;
        }
        &:hover{
            .luminix-blog-thumb2::before{
                opacity: 1!important;
            }
            .luminix-blog-thumb2 img{
                transform: scale(1.1);
            }
        }
        &.wrap4{
            margin-bottom: 30px;
            transition: 0.4s;
            border: 1px solid #001A3D1A;
            &:last-child{
                margin-bottom: 0!important;
            }
            &:hover{
                box-shadow: 0px 4px 80px 0px #00000014;
                border: 1px solid var(--white-bg);
            }
        }
    }
    &-thumb2{
        position: relative;
        overflow: hidden;
        border-radius: 10px 10px 0 0;
        img{
            border-radius: 10px 10px 0 0;
            width: 100%;
            transition: all 0.4s;
            object-fit: cover;
        }
        &::before{
            position: absolute;
            content: "";
            top: 0;
            z-index: 1;
            left: 0;
            width: 100%;
            height: 100%;
            opacity: 0;
            transition: all 0.4s;
            background: linear-gradient(180deg, rgba(41, 32, 210, 0) 0%, rgba(41, 32, 210, 0.6) 100%);
        }
    }
    &-content2{
        padding: 30px;
        border-radius: 0 0 10px 10px;
        h5{
            padding-bottom: 15px;
        }
        p{
            font-size: 16px;
            line-height: 24px;
            margin-bottom: 0;
            padding-bottom: 24px;
        }
        p.data{
            padding-top: 15px;
            color: var(--heading-color);
        }
    }
    // end two
    &-wrap3{
        display: grid;
        align-items: center;
        gap: 24px;
        background: var(--white-bg);
        border-radius: 10px;
        margin-bottom: 24px;
        grid-template-columns: 1fr 1fr;
        @include max (767px){
            display: block;
        }
        &:last-child{
            margin-bottom: 0;
        }
        &:hover{
            .luminix-blog-thumb3{
                &::before{
                    opacity: 1;
                }
                img{
                    transform: scale(1.1);
                }
            }
        }
        
    }
    &-thumb3{
        position: relative;
        height: 100%;
        overflow: hidden;
        border-radius: 10px 0 0 10px;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: all 0.4s;
            border-radius: 10px 0 0 10px;
            @include max (767px){
                border-radius: 10px;
            }
        }
        &::before{
            position: absolute;
            content: "";
            top: 0;
            z-index: 1;
            left: 0;
            width: 100%;
            height: 100%;
            opacity: 0;
            transition: all 0.4s;
            border-radius: 10px;
            background: linear-gradient(180deg, rgba(41, 32, 210, 0) 0%, rgba(41, 32, 210, 0.6) 100%);
        }
    }
    &-content3{
        padding: 20px 18px 20px 0;
        @include max (767px){
            padding: 30px;
        }
        h5{
            padding-bottom: 15px;
        }
        p{
            font-size: 16px;
            line-height: 24px;
            margin-bottom: 0;
            padding-bottom: 24px;
            color: var(--heading-color);
        }
    }
    // start inner page
    &-sidebar{
        @include max(991px) {
          margin-top: 40px;
        }
    }
    &-widgets{
        padding: 24px;
        margin-bottom: 24px;
        border-radius: 10px;
        background-color: var(--light-color);
        &:last-child{
        margin-bottom: 0;
        }
        img.line{
        margin-top: -15px;
        }
    }
    &-categorie{
        padding-top: 5px;
        ul{
            li{
            padding-bottom: 12px;
            border-bottom: 1px solid #001A3D1A;
            margin-bottom: 12px;
            &:last-child{
                padding-bottom: 0;
                border-bottom: none;
                margin-bottom: 0;
            }
            a{
                display: flex;
                transition: all 0.4s;
                justify-content: space-between;
                color: var(--heading-color);
                span{
                font-weight: 500;
                }
                &:hover{
                color: var(--accent-color);
                }
            }
            }
        }
    }
    &-tags{
        padding-top: 5px;
        ul{
            li{
                margin-right: 4px;
                margin-bottom: 8px;
                display: inline-block;
                a{
                    padding: 8px 16px;
                    display: block;
                    border-radius: 50px;
                    transition: all 0.4s;
                    color: var(--light-color4);
                    background-color: var(--white-bg);            
                    &:hover{
                        color: var(--white-color);
                        background-color: var(--accent-bg);
                    }
                }
            }
        }
        &.tags2{
            display: flex;
            gap: 15px;
            padding-top: 0;
            align-items: center;
            ul{
                li{
                    margin-bottom: 0;
                    a{
                        background-color: var(--light-color);
                        &:hover{
                            background-color: var(--accent-bg);
                        }
                    }
                }
            }
        }
    }
    &-d-thumb{
        position: relative;
        overflow: hidden;
        border-radius: 10px;
        &:hover{
            img{
                transform: scale(1.1);
            }
        }
        img{
            border-radius: 10px;
            width: 100%;
            height: 100%;
            transition: all 0.4s;
            object-fit: cover;
        }
    }
    &-d-content{
        p{
            color: var(--heading-color);
            margin-bottom: 30px;
        }
    }
    &-d-content2{
        p{
            color: var(--heading-color);
            margin-bottom: 24px;
            span{
                font-family: $heading-font;
                font-size: 20px;
                font-weight: 700;
                line-height: 30px;
            }
        }
    }
    &-d-thumb2{
        margin: 10px 0 30px 0;
        position: relative;
        overflow: hidden;
        border-radius: 10px;
        &:hover{
            img{
                transform: scale(1.1);
            }
        }
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: all 0.4s;
            border-radius: 10px;
        }
    }
    &-d-social {
        display: flex;
        align-items: center;
        gap: 15px;
    }
    &-d-tag-wrap {
        display: flex;
        align-items: center;
        gap: 15px;
        flex-wrap: wrap;
        justify-content: space-between;
        border-top: 1px solid #001A3D1A;
        padding: 20px 0;
        border-bottom: 1px solid #001A3D1A;
        margin-bottom: 30px;
    }
    &-d-comment-box{
        h5{
            padding-bottom: 30px;
        }
    }
    &-d-comment-wrap1{
        display: flex;
        .reply-btn a{
            font-size: 16px;
            font-weight: 700;
            line-height: 24px;
            transition: all 0.4s;
            color: var(--heading-color);
            padding-top: 10px;
            &:hover{
                color: var(--accent-color);
            }
        }
    }
    &-d-comment-wrap{
        display: flex;
        padding-bottom: 24px;
        border-bottom: 1px solid rgba(0,26,61,.1019607843);
        margin-bottom: 30px;
        .reply-btn a{
            font-size: 16px;
            font-weight: 700;
            line-height: 24px;
            transition: all 0.4s;
            color: var(--heading-color);
            padding-top: 10px;
            &:hover{
                color: var(--accent-color);
            }
        }
    }
    &-d-comment-thumb{
        margin-right: 20px;
        flex-shrink: 0;
        img{
            border-radius: 100px;
        }
    }
    &-d-comment-data1{
        padding-bottom: 24px;
        border-bottom: 1px solid rgba(0,26,61,.1019607843);
        margin-bottom: 24px;
        h6{
            font-family: $body-font;
            font-size: 16px;
            font-weight: 700;
            line-height: 24px;
            margin-bottom: 5px;
            color: var(--heading-color);
        }
        span{
            font-family: $body-font;
            font-size: 16px;
            font-weight: 400;
            line-height: 24px;
            color: var(--dark-color1);
        }
        p{
            color: var(--dark-color1);
            padding-top: 10px;
        }
    }
    &-d-comment-data{
        h6{
            font-family: $body-font;
            font-size: 16px;
            font-weight: 700;
            line-height: 24px;
            margin-bottom: 5px;
            color: var(--heading-color);
        }
        span{
            font-family: $body-font;
            font-size: 16px;
            font-weight: 400;
            line-height: 24px;
            color: var(--dark-color1);
        }
        p{
            color: var(--dark-color1);
            padding-top: 10px;
        }
    }
    &-d-comment-box2{
        h5{
            padding-bottom: 30px;
        }
    }
}
.pl-101{
    padding-left: 101px;
    @include max (767px){
        padding-left: 0;
    }
}

// sidebar

.mr-100{
    margin-right: 120px;
    @include max (991px){
        margin-right: 0;
    }
}
  
.luminix-search-box{
    position: relative;
    input{
    padding: 15px!important;
    padding-left: 50px!important;
    height: 60px;
    font-size: 16px;
    color: #0A102F!important;
    line-height: 24px;
    border-radius: 50px!important;
    border: 1px solid #001A3D1A!important;
    background-color: var(--white-bg)!important;
    &::placeholder{
        color: #0A102F;
        }
        &:focus{
            border: 1px solid var(--accent-color)!important;
        }
    }
}
button#luminix-search-btn {
    position: absolute;
    left: 20px;
    top: 16px;
    i{
      font-size: 24px;
      color: var(--heading-color);
    }
}
.pt-15{
    padding-top: 15px;
}
  
.luminix-recent-post {
    &-item{
        display: flex;
        gap: 15px;
        align-items: center;
        margin-bottom: 20px;
      &:first-child{
        padding-top: 5px;
      }
      &:last-child{
        margin-bottom: 0;
      }
      &:hover{
        .luminix-recent-post{
            &-thumb img{
                transform: scale(1.1);
            }
        }
        
      }
    }
    &-thumb{
        border-radius: 10px;
        overflow: hidden;
        flex-shrink: 0;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: all 0.4s;
        }
    }
    &-data{
      h6{
        font-family: $body-font;
        font-size: 16px;
        font-weight: 600;
        line-height: 24px;
        color: var(--light-color4);
        padding-top: 5px;
      }
      span{
        font-size: 14px;
        line-height: 20px;
        color: var(--accent-color);
        img{
            margin-top: -5px;
            margin-right: 6px;
        }
      }
    }
    
}
  
blockquote{
    position: relative;
    border-radius: 10px;
    margin: 0;
    z-index: 0;
    background: linear-gradient(90deg, rgba(41, 32, 210, 0.1) 0%, rgba(41, 32, 210, 0) 100%);
    p{
        font-family: $blockquote-font;
        padding: 28px 30px;
        font-size: 26px;
        font-weight: 400;
        line-height: 34px;
        letter-spacing: -0.01em;
        color: var(--accent-color)!important;
    }
    img{
        z-index: 1;
        position: absolute;
    }
}
  
 

  
  