.luminix-cta{
    &-title{
        padding: 50px;
        background: var(--accent-bg);
        border-radius: 8px;
        margin-bottom: 100px;
        h2{
            color: var(--white-color);
        }
        @include max (1199px){
            text-align: center;
         }
        @include max (991px){
            margin-bottom: 80px;
        }
        @include max (767px){
            padding: 40px;
            margin-bottom: 60px;
        }
    }
    // end footer cta
    &-section{
        padding: 120px 0;
        position: relative;
        &::before{
            content: "";
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            position: absolute;
            background-color: #2920D299;
        }
        @include max (991px){
            padding: 80px 0;
        }
        @include max (767px){
            padding: 60px 0;
        }
    }
    &-wrap{
        max-width: 950px;
        text-align: center;
        margin: 0 auto;
        position: relative;
        h6{
            color: var(--white-color);
            padding-bottom: 15px;
        }
        h2{
            color: var(--white-color);
        }
    }
    &-btn-title{
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 24px;
        flex-wrap: wrap;
    }
}

